.logistics {
    padding-top: 309px;
    padding-bottom: 100px;

    &__group {
        max-width: 1170px;
        max-width: 1170px;
        display: flex;
        justify-content: space-between;
        margin: auto;

        &--accordion {
            max-width: 570px;
            min-height: 533px;
            background-color: #ba0000;
            width: 100%;
            padding-top: 90px;
            padding-bottom: 40px;
            padding-left: 40px;
            padding-right: 40px;
        }

        &--img {
            padding-top: 34px;

            &::before {
                content: "";
                position: absolute;
                width: 431px;
                height: 499px;
                border: 20px solid #ffcc00;
                transform: translateY(-34px) translateX(-34px);
                z-index: -1;
            }
        }
    }
}

.title {
    display: flex;
    align-items: center;
    padding-left: 21px;
    color: #ba0000;
    font-size: 14px;
    font-weight: 700;
    line-height: 24px;
    text-transform: uppercase;
    letter-spacing: 0.42px;
}

.icon {
    max-width: 50px;
    min-height: 50px;
    border-left: 2px solid #ba0000;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #bb0200;
}


.accordion-item {
    border: 2px solid #ffffff;
    background-color: #a30000;
    margin-top: 18px;

    // border-top: 0;
    &:nth-child(1) {
        margin-top: 0;
    }
}

.accordion-item-active {
    // max-width: 440px;
    border: 2px solid #ffcc00;
    background-color: #a30000;
    margin-top: 18px;
    border-top: 0;

    &:nth-child(1) {
        margin-top: 0;
    }
}

.item-header {
    display: flex;
    background-color: #ffcc00;
    justify-content: space-between;
}

.text {
    color: #ffffff;
    font-size: 16px;
    font-weight: 700;
    line-height: 24px;
    letter-spacing: 1.6px;
    padding: 22px;
    padding-top: 19px;
    list-style: none;
    display: none;

    &-title {
        font-size: 16px;
    }

    &-item {
        // margin-left: 25px;
        font-weight: 400;
        padding-bottom: 5px;

        &::before {
            content: "►";
            color: #ffcc00;
            margin-right: 12px;
        }
    }
}
.accordion-item.active .text{
    display: block;
    transition: 1s linear;
}