.hero {
    background: linear-gradient(45deg, #ba0000 0%, rgba(255, 204, 0, 0.01) 99%, rgba(255, 204, 0, 0) 100%), url(../img/jpg/hero_bg.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    padding: 180px 0;

    &-title {
        color: #ffffff;
        max-width: 440px;
        min-height: 155px;
        line-height: 80px;
        letter-spacing: 2.1px;
        padding-bottom: 84px;
    }

    &-txt {
        max-width: 747px;
        min-height: 50px;
        color: #ffcc00;
        font-size: 20px;
        font-weight: 400;
        line-height: 30px;
        letter-spacing: 0.6px;
        position: relative;
        padding-bottom: 40px;

        &::before {
            content: "";
            display: block;
            width: 100px;
            height: 4px;
            background-color: #ffcc00;
            position: absolute;
            top: -38px;
        }
    }

    &-menu {
        max-width: 453px;
        justify-content: space-between;
        display: -ms-flexbox;
        display: flex;
        color: #ffffff;
        font-size: 20px;
        font-weight: 700;
        line-height: 30px;
        text-transform: uppercase;
        padding-bottom: 40px;

        &--item {

            & span {
                color: #ffcc00;
                letter-spacing: 0.6px;
                margin-left: 27px;
            }
        }
    }

    &-btn1 {
        box-shadow: 0 10px 18px rgba(255, 204, 0, .4);
        border-radius: 50px;
        color: #ba0000;
        font-size: 12px;
        font-weight: 800;
        text-transform: uppercase;
        letter-spacing: 1.2px;
        padding: 20px 31px;
        background-color: #ffcc00;
    }

    &-btn2 {
        border-radius: 50px;
        border: 2px solid #ffffff;
        background-color: transparent;
        text-transform: uppercase;
        color: #fff;
        font-size: 12px;
        font-weight: 800;
        padding: 20px 37px;
        letter-spacing: 1.2px;
        margin-left: 20px;
    }
}