.best {
    background: linear-gradient(to top, rgba(255, 204, 0, 0.7) 0%, rgba(186, 0, 0, 0) 100%), url(../img/jpg/international-service.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    padding-top: 180px;
    padding-bottom: 180px;

    &__title {
        color: #ffffff;
        font-size: 50px;
        font-weight: 800;
        letter-spacing: 1.5px;
        display: flex;
        justify-content: center;
        padding-bottom: 50px;
    }

    &__play {
        width: 70px;
        height: 70px;
        box-shadow: 0 10px 18px rgba(255, 204, 0, .4);
        border-radius: 35px;
        border: 2px solid #ffcc00;
        background-color: #ffcc00;
        align-items: center;
        color: #ba0000;
        margin: 0 auto;
        align-items: center;
        display: flex;
        justify-content: center;
        margin-bottom: 178px;
    }

    &__cards {
        display: flex;
        justify-content: space-between;
        max-width: 1170px;
        margin: auto;
        transform: translateY(394px);
        & .card {
            box-shadow: 0 10px 18px rgba(191, 191, 191, 0.4);
            background-color: #ffffff;

            &__title {
                color: #ba0000;
                font-size: 20px;
                font-weight: 800;
                line-height: 24px;
                letter-spacing: 0.6px;
                padding: 30px;
            }

            &__list {
                padding-left: 30px;
                padding-bottom: 30px;
                list-style: none;


                &--item {
                    color: #777777;
                    font-size: 16px;
                    font-weight: 400;
                    line-height: 24px;
                    &::before{
                        content: "►";
                        color: #ffcc00;
                        margin-right: 12px;
                    }
                }
            }
        }
    }
}