.placeholder {
    padding-top: 70px;
    padding-bottom: 70px;

    &__img {
        width: 270px;
        height: 129px;
        border: 2px solid rgba(186,0,0,.1);
        display: flex;
        justify-content: center;
        transition: .25s;
        &:hover, &-active {
            border: 2px solid #ba0000;
        }
    }
}