.excellence {
    background-color: #ba0000;
    padding-top: 100px;
    padding-bottom: 100px;

    &__title {
        color: #ffffff;
        font-family: Poppins, sans-serif;
        font-size: 30px;
        // font-weight: 400;
        line-height: 24px;
        text-transform: uppercase;
        padding-bottom: 41px;
    }

    &__txt {
        color: #ffffff;
        font-family: Poppins, sans-serif;
        font-size: 16px;
        font-weight: 400;
        line-height: 24px;
        letter-spacing: 0.48px;
        max-width: 739px;
    }

    &__btn {
        width: 150px;
        height: 50px;
        border-radius: 25px;
        background-color: #a00000;
        color: #ffffff;
        font-size: 12px;
        font-weight: 400;
        text-transform: uppercase;
        letter-spacing: 1.2px;
    }
}
.excellence__group{
    display: flex;
    justify-content: space-between;
}