.main-menu {
    background-color: #ffcc00;
    padding: 39px 0;
}

.navigations {
    font-family: Poppins-regular, sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 24px;
    text-transform: uppercase;
    color: #ba0000;
    max-width: 654px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;

    &-item {
        transition: ease-in-out .25s;
        position: relative;
        &:hover{
             font-family: Poppins, sans-serif;
            font-weight: 800;
            letter-spacing: 0.42px;
        }

        &:hover::after {
          
            width: calc(30px + (100%));
            height: 4px;
            background-color: #ba0000;
        }

        &::after {
            content: "";
            display: block;
            position: absolute;
            bottom: -52px;
            left: -15px;
            transition: ease-out .25s;
            
        }
    }


}

.search {
    width: 50px;
    height: 50px;
    // box-shadow: 0 10px 18px rgba(186, 0, 0, 0.4);
    border-radius: 25px;
    background-color: #ba0000;
    position: relative;

    &::before {
        content: "";
        position: absolute;
        box-shadow: 0 10px 18px rgba(186, 0, 0, .4);
        display: block;
        width: 50px;
        height: 50px;
        border-radius: 50%;
        top: 0px;
    }
}

.fa.fa-search {
    color: #ffffff;

}