.header {
  max-width: 1920px;
  // height: 50px;
  background-color: #ba0000;
  color: #ffffff;
  font-family: "Poppins - Regular", sans-serif;
  font-size: 14px;
  font-weight: 400;
  // line-height: 24px;
  padding: 14px 0;

}

.fa.fa-users {
  padding-right: 6px;
}

.wrapper {
  &_group {
    max-width: 287px;
    width: 100%;
    display: flex;
    justify-content: space-between;
  }

  &-txt {
    color: #ffffff;
    // font-family: Poppins, sans-serif;
    font-size: 14px;

    // font-weight: 400;
    // line-height: 24px;
    & img {
      position: absolute;
      padding-left: 4px;
    }
  }

  &_group-select {
    display: flex;
    & img {
      padding-left: 4px;
    }
    color: #ffffff;
    // font-family: Poppins, sans-serif;
    font-size: 14px;
    // font-weight: 400;
    // line-height: 24px;
    background-color: inherit;
    border: none;
  }
}