.awesome {
    padding-top: 100px;
    padding-bottom: 100px;
    background-color: #fff;

    &-title {
        display: block;
        max-width: 752px;
        min-height: 109px;
        // position: absolute;
        color: #f8f8f8;
        font-size: 150px;
        // font-weight: 400;
        line-height: 80px;
        text-transform: uppercase;
        letter-spacing: 4.5px;
        position: relative;
        padding-bottom: 79px;

        &::after {
            content: "Awesome Services";
            position: absolute;
            color: #ba0000;
            font-size: 50px;
            font-weight: 800;
            line-height: 80px;
            letter-spacing: 1.5px;
            top: 0;
            text-transform: none;
            left: 0;
        }
    }
}

.cards {
    display: flex;
    justify-content: space-between;

    &__goods {
        max-width: 270px;

        &--dhl {
            max-width: 270px;
            min-height: 339px;
            background-color: #ba0000;
            padding: 30px;

            &-select {
                // padding-bottom: 20px;
                padding: 20px;
                background-color: #9d0000;
                color: #ffffff;
                font-size: 16px;
                font-weight: 400;
                letter-spacing: 0.48px;
                position: relative;
                line-height: 11px;
            }

            &-btn {
                box-shadow: 0 10px 18px rgba(255, 204, 0, 0.4);
                border-radius: 50px;
                background-color: #ffcc00;
                color: #ba0000;
                font-size: 12px;
                font-weight: 800;
                text-transform: uppercase;
                letter-spacing: 1.2px;
                padding: 20px 45px;
                margin-top: 20px;
            }

            & h3 {
                color: #ffffff;
                font-size: 20px;
                letter-spacing: 0.6px;
                padding-bottom: 62px;
            }

            & p {
                color: #ffffff;
                font-size: 16px;
                font-weight: 400;
                line-height: 26px;
                letter-spacing: 0.48px;
                position: relative;
                padding-bottom: 39px;

                &::before {
                    content: "";
                    display: block;
                    position: absolute;
                    width: 70px;
                    height: 2px;
                    background-color: #ffcc00;
                    top: -30px;
                }
            }
        }

        &--img {
            padding-bottom: 30px;
        }

        &--title {
            color: #ba0000;
            font-size: 20px;
            text-transform: uppercase;
            letter-spacing: 0.6px;
            padding-bottom: 20px;
        }

        &--txt {
            color: #bfbfbf;
            font-size: 16px;
            font-weight: 400;
            line-height: 26px;
            letter-spacing: 0.48px;
            padding-bottom: 20px;
        }

        &--read {
            color: #222222;
            font-size: 16px;
            font-weight: 800;
            line-height: 26px;
        }
    }
}

.fa.fa-caret-right {
    color: #bc0606;
}